%rangeSlider---rangeSlider__fill {
  background: #ffffff;
  display: block;
  width: 100%;
  border-radius: 10px;
  height: 3px;
}

@include component("range_slider") {
  @include block(".rangeSlider") {
    @extend %rangeSlider---rangeSlider__fill;
    position: relative;

    @include modifiy("disabled") {
      opacity: 0.4;
    }

    @include element("fill") {
      @extend %rangeSlider---rangeSlider__fill;
      background: #3367D6;
      position: absolute;
      top: 0;
      z-index: 2;
    }

    @include element("handle") {
      background-color: #ffffff;
      border: 1px solid #ccc;
      cursor: pointer;
      display: inline-block;
      position: absolute;
      z-index: 3;
      background-image: -webkit-linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.1));
      background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.1));
      border-radius: 50%;
      width: 15px;
      height: 15px;
      top: -5px;

      &:after {
        content: "";
        display: block;
        margin: auto;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.13), rgba(255, 255, 255, 0));
        background-image: linear-gradient(rgba(0, 0, 0, 0.13), rgba(255, 255, 255, 0));
        border-radius: 50%;
        width: 7px;
        height: 7px;
      }

      &:active {
        background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.12));
        background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.12));
      }

      input[type="range"]:focus + & {
        box-shadow: 0 0 8px rgba(142, 68, 173, 0.9);
      }
    }

    @include element("buffer") {
      z-index: 1;
      position: absolute;
      top: 0;
      height: 3px;
      background: #7396e3;
      border-radius: 10px;
    }
  }
}

@include component("audio-player") {
  @include block(".audio-player") {
    @include modifiy("default") {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      padding: 20px 20px;

      @include element("control") {
        width: 70vw;
        background-color: rgba(#ffffff, 0.3);
        padding: 20px 20px;
      }
    }
  }
}